.switcher {
  display: grid;
  grid-template-columns: 49px 1fr 49px;
  gap: 13px;
  padding: 0 35px;
  align-items: center;
  position: relative;

  @media (min-width: $tablet-width) {
    padding: 0 60px;
    grid-template-columns: 70px 1fr 70px;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}


.switcher__bar {
  height: 14px;
  background-color: $white;
  border: 1px solid $special_grey;
  grid-column: 2 / 3;
  grid-row: 1 / 2;
  position: relative;
  border-radius: 6px;
  &::after {
    position: absolute;
    top: 3px;
    width: 36px;
    content: "";
    height: 6px;
    border-radius: 3px;
    background-color: #68B738;
  }
  @media (min-width: $tablet-width) {
    display: none;
  }
}

.switcher__bar--before::after {
  left: 6px;
  right: auto; // для переключения из положения --after (
}

.switcher__bar--after::after {
  left: auto;
  right: 6px;
}


.switcher__range {
  display: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  grid-column: 2 / 3;
  grid-row: 1 / 2;

  @media (min-width: $tablet-width) {
    display: block;
  }
}


// для хрома
.switcher__range::-webkit-slider-runnable-track {  // стилизация полоски ползунка
  border-radius: 6px;
  height: 6px;
  background-color: #dcdcdc;
}

.switcher__range::-webkit-slider-thumb { // стилизация полоски ползунка
  border-radius: 50%;
  cursor: pointer;
  width: 34px;
  height: 34px;
  -webkit-appearance: none;
  margin-top: -14px;
  background-color: $color-primary; // зеленая точка внутри ползунка
  box-shadow: inset 0 0 0 1px $special_grey, // граница в 1рх по краю
              inset 0 0 0 13px $white, // белый фон накрывает зеленый бекграунд на 13рх внутрь от края
              0px 1px 3px rgba(0, 0, 0, 0.16); // обычная тень вниз
}


//для мозилы
.switcher__range::-moz-range-track {
  border-radius: 6px;
  height: 6px;
  background-color: #dcdcdc;
}

.switcher__range::-moz-range-thumb {
  border-radius: 50%;
  cursor: pointer;
  width: 34px;
  height: 34px;
  -webkit-appearance: none;
  margin-top: -14px;
  background-color: $color-primary; // зеленая точка внутри ползунка
  box-shadow: inset 0 0 0 1px $special_grey, // граница в 1рх по краю
              inset 0 0 0 13px $white, // белый фон накрывает зеленый бекграунд на 13рх внутрь от края
              0px 1px 3px rgba(0, 0, 0, 0.16); // обычная тень вниз
}

.switcher__button {
  display: block;
  padding: 0 2px 2px; // непонятно как получить это значение

  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  color: $black;
  background-color: transparent;
  border: none;

  &:last-child { //  определяем "стало" в третий стобик
    grid-column: 3/4;
  }
  @media (min-width: $tablet-width) {
    padding-bottom: 4px;
    border-bottom: 1px dotted rgba($black, 0.25);
    justify-self: center;
    font-size: 20px;
    line-height: normal;
  }
}
